



















import Vue from "vue";
import Header from "../components/Header.vue";
import InstructionBlock from "../components/InstructionBlock.vue";
import Footer from "../components/Footer.vue";
import FullwidthButton from "../components/FullwidthButton.vue";
import Game from "../components/Game.vue";

export default Vue.extend({
  components: {
    Header,
    InstructionBlock,
    Footer,
    FullwidthButton,
    Game
  },
  data: function() {
    return {
      game: false,
      finished: false,
      level: 1,
      levels: 2,
      nextLevelEnabled: false
    };
  },
  methods: {
    startGame: function() {
      this.game = true;
    },
    goBack: function() {
      if (this.level > 1) {
        this.level--;
      } else {
        this.game = false;
      }
    },
    goNext: function() {
      this.level++;
      this.nextLevelEnabled = false;
    },
    enableNextLevel: function() {
      if (this.level < this.levels) {
        this.nextLevelEnabled = true;
      }
    }
  },
  computed: {
    headerTitle: function(): string {
      return this.game ? `Level ${this.level}` : "Boxgame";
    }
  }
});
